::-webkit-scrollbar{width:6px}::-webkit-scrollbar-track{background:#f1f1f1}::-webkit-scrollbar-thumb,::-webkit-scrollbar-thumb:hover{background:#7bd2d1}#root{display:grid;justify-content:center;align-content:center;width:100%;height:100%}.title{padding-left:35px}.container{width:740px;background-color:#fff;border:1.3px solid #6f6f6f;position:relative;top:50%;-webkit-transform:translateY(-50%);transform:translateY(-50%);height:400px;border-radius:3px}.header{float:right;position:relative;width:100%;height:50px;background-color:#7bd2d1;font-family:monospace;font-size:12px;color:#f5f5f5;font-weight:700;line-height:15px}.directView{margin-top:10px}.heading-card{margin:17px 0 0;display:grid;grid-template-columns:40% 60%}.card{border:1.4px solid #6f6f6f;width:250px;height:35px;margin-left:35px;line-height:35px;padding:0 25px;color:#565656;border-radius:3px;font-family:Aleo}.input-box{padding:0 7px;margin-left:35px;width:200px;border:1.5px inset #d7d7d7;margin-right:8px;border-radius:2px;height:17px}.left-container{position:relative;overflow:auto;height:340px;width:375px;display:inline-block}button{border:.5px solid #7bd2d1;background-color:#7bd2d1;border-radius:3px;width:70px;height:22px;color:#fff;font-family:monospace;font-weight:700;font-size:14px;box-shadow:1.5px 2px #bebebe}.right-container{position:relative;overflow:auto;height:350px;width:362px;display:inline-block;border-left:1px solid #6f6f6f;font-family:Aleo}h2,h3{color:#765a42;margin:18px 10px 7px}.food-head{color:#765a42;display:inline-block;font-family:Aleo;font-weight:bolder;font-size:23px;margin:15px 0 0}.food-img{display:inline-block;width:70px;border:1.5px solid #7bd2d1;border-radius:50px;height:70px}.inner-right-container{padding:0 20px;height:95%}.separate{display:grid;grid-template-rows:1fr;grid-template-columns:1fr 1fr}.each-left{margin:0 1em;font-size:11.5px}.each-right{margin:0 0 5px 10px;font-size:13.5px}.left{grid-column:1}.right{grid-column:2}
